import React, { useState, useEffect, useRef } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import { useNavigate, useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { IoAddOutline } from "react-icons/io5";
import DataTable from "react-data-table-component";
import { FaTrashAlt, FaEdit } from "react-icons/fa";
import axios from "axios";
import ReactQuill from "react-quill";
import { Modal, Button } from "react-bootstrap";

import SuccessAlert from "../../../components/Admin/SuccessAlert";
import "react-quill/dist/quill.snow.css";
function AdminDuediligenceCategoryadd() {
  const quillRef = useRef();
  const navigate = useNavigate();
  const [isOpen, setIsOpen] = useState(false);
  const [successMessage, setsuccessMessage] = useState("");
  const [spinners, setspinnsers] = useState(false);
  const [deleteId, setdeleteId] = useState("");
  const [show, setShow] = useState(false);
  const { id } = useParams();
  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  useEffect(() => {
    document.title = "Add Datarooms Tip - Admin";
  }, []);
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const [records, setRecords] = useState([]);
  useEffect(() => {
    getvideo();
  }, [id]);
  const getvideo = async () => {
    let formData = {
      id: id,
    };
    try {
      const res = await axios.post(apiUrl + "getsubcategorylist", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const [maxLimit, setMaxLimit] = useState("");
  const [file, setFile] = useState(null);
  const [error, setError] = useState("");

  const handledelete = (id) => {
    setdeleteId(id);
    setShow(true);
  };
  const columns = [
    {
      name: "Name",
      selector: (row) => row.name,
      sortable: true,
      className: "age",
    },

    {
      name: "View",
      cell: (row) => (
        <>
          <div className="d-flex align-items-center gap-2">
            <Link
              className=" fs-5"
              type="button"
              onClick={() => handleaddtip(row.id, row.tips)}
              title="Add Tip"
            >
              <IoAddOutline />
            </Link>
            <button
              onClick={() => handledelete(row.id)}
              type="button"
              className="dataedit_btn text-danger border-0 fs-5"
              title="Delete"
            >
              <FaTrashAlt />
            </button>
          </div>
        </>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const [showEdit, setshowEdit] = useState(false);

  const handleaddtip = (idd, tipss) => {
    console.log(idd);
    setshowEdit(true);
    setdeleteId(idd);
    setContentsharing(tipss);
    setsuccessMessage("");
  };
  const [errorset, seterror] = useState("");
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (contentsharing.trim() === "") {
      seterror("This field is required");
      return;
    } else {
      seterror("");
      console.log(contentsharing);
      let formData = {
        texttip: contentsharing,
        id: deleteId,
      };
      setspinnsers(true);
      try {
        const res = await axios.post(apiUrl + "savedataroomtip", formData, {
          headers: {
            Accept: "application/json",
          },
        });
        var respo = res.data.results;
        console.log(respo);
        setsuccessMessage("Updated successfully");
        // setContentsharing("");
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    }
  };
  const handleClose = () => {
    setshowEdit(false);
  };
  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  const [contentsharing, setContentsharing] = useState("");
  const handleChangesharing = (value) => {
    setContentsharing(value);
  };
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <div className="card p-3">
                      <div className="d-flex justify-content-between mb-3">
                        <h5 className="mb-3">Data Room Sub Category</h5>
                      </div>

                      <div className="d-flex justify-content-end align-items-end">
                        <div class="search-bar">
                          <input
                            type="text"
                            placeholder="Search..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                          <span class="search-icon">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                              />
                            </svg>
                          </span>
                        </div>
                      </div>
                      <DataTable
                        columns={columns}
                        data={filteredRecords}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                        noDataComponent={
                          <div className="text-center">
                            <span>No results found</span>
                          </div>
                        }
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      <Modal className="text-white" show={showEdit} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Update</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row g-4">
            {successMessage && (
              <SuccessAlert
                message={successMessage}
                onClose={() => setsuccessMessage("")}
              />
            )}
            <div className="col-sm-12">
              <div className="bglight rounded h-100">
                <form
                  action="javascript:void(0)"
                  onSubmit={handleSubmit}
                  method="post"
                  className="d-flex flex-column gap-3"
                >
                  <div className="text-start mb-4">
                    <label
                      htmlFor="exampleInputLimit"
                      className="form-label text-dark"
                    >
                      Tip <span className="text-danger">*</span>
                    </label>
                    <ReactQuill
                      ref={quillRef}
                      required
                      value={contentsharing}
                      onChange={handleChangesharing}
                      style={{ height: "200px" }}
                      theme="snow"
                    />
                  </div>
                  <span className="text-danger mt-2">{errorset}</span>
                  <div className="d-grid mb-4">
                    <button type="submit" className="btn btn-primary">
                      Add
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default AdminDuediligenceCategoryadd;
